const sexes = ['he', 'she', 'they']

interface IDictionary {
  dear: string
  you: string[]
  support: string
  can: string
}

const wordsMap: Object = {
  he: {
    dear: '',
    you: ['', '', '', ''],
    support: '',
    can: '',
  },
  she: {
    dear: '',
    you: ['', '', '', ''],
    support: '',
    can: '',
  },
  they: {
    dear: '',
    you: ['', '', '', ''],
    support: '',
    can: '',
  },
}

export const useWords = (gender: string = sexes[0]): IDictionary => {
  return wordsMap[gender]
}
